@use "sass:list";

.calendar {
  padding: 10px;
  width: ($day-size * list.length($week-days)) + ($gap-size * (list.length($week-days) - 1));

  // flex-flow: row wrap;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  gap: $gap-size;

  &__day {
    box-sizing: border-box;
    height: $day-size;
    width: $day-size;
    border: 1px solid #000;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: #eee;

    @for $i from 1 through 31 {
      &:nth-child(#{$i})::before {
        content: '#{$i}';
      }
    }

    &:hover {
      cursor: pointer;
      background-color: #ffbfcb;
      transform: translateY(-20px);
      transition: 0.5s;
    }
  }

  @for $i from 28 through 31 {
    &--month-length-#{$i} {
      @for $j from ($i + 1) through 32 {
        .calendar__day:nth-child(#{$j}) {
          display: none;
        }
      }
    }
  }

  @each $day in $week-days {
    &--start-day-#{$day} {
      & > :first-child {
        margin-left: (list.index($week-days, $day) - 1) * ($day-size + $gap-size);
      }
    }
  }
}
